<template>
    <div class="random-links">
      <div v-if="$route.name === '推广链接'">
        <Button type="success" size="large" to="/manage/links/spread/create" class="create">添加新链接</Button>
        <RadioGroup class="tuiguang-tag" v-model="tuiguangTag" type="button" @on-change="showDataList">
          <Radio label="全部"></Radio>
          <Radio v-for="(item, index) in tagList" :label="item.name"></Radio>
        </RadioGroup>
        <Table class="spread-table" :columns="spreadHeader" :data="spreadData"></Table>
        <Page show-total :total="dataList.length" :page-size="10" @on-change="changePage" />
      </div>
      <router-view></router-view>
    </div>
</template>

<script>
export default {
  data() {
    return {
      spreadData: [],
      isShow: false,
      isUpdate: false,
      dataList: [],
      domain_names: [],
      spreadHeader: [
        {
          title: "链接",
          align: "center",
          render: (h, params) => {
            let url = 'http://doumobone.top/transfer/' + params.row.id
            return h( "a", {
                attrs: {
                  href: url,
                  target: '_blank'
                }
              }, url);
          }
        },
        {
          key: "group",
          title: "分组",
          align: "center",
          width: 120
        },
        {
          key: "title",
          title: "标题",
          align: "center"
        },
        {
          key: "remarks",
          title: "备注",
          align: "center"
        },
        {
          title: "数据",
          align: "center",
          render: (h, params) => {
            let url = "http://test.oorggt.top/tuiguang/data?tid=" + params.row.id;
            return h( "Button", {
              props: {
                type: "primary",
                size: "small"
              },
              on: {
                click() {
                  window.open(url);
                }
              }
            }, '查看数据');
          }
        },
        {
          title: "置顶",
          align: "center",
          render: (h, params) => {
            let that = this;
            return h( "Button", {
              props: {
                type: "info",
                size: "small"
              },
              on: {
                click() {
                  that.$Modal.confirm({
                    title: '提示:',
                    content: '确认置顶这条内容吗?',
                    onOk: () => {
                      that.$axios.post('/transfer/goTop', {id: params.row._id})
                        .then(res => {
                          that.$Message.info(res.data.success);
                          that.showDataList();
                        });
                    }
                  })
                }
              }
            }, '置顶');
          }
        },
        {
          title: "Action",
          align: "center",
          width: 200,
          render: (h, params) => {
            let that = this;
            return h("div", [
              h( "Button", {
                  style: {
                    marginRight: "10px"
                  },
                  props: {
                    type: "primary",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.$router.push({
                        name: "修改推广链接",
                        params: {
                          row: params.row,
                          index: params.index
                        }
                      });
                    }
                  }
                }, "修改" ),
              h( "Button", {
                style: {
                  marginRight: "10px"
                },
                  props: {
                    type: "error",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.deleteOne(params.row, params.index);
                    }
                  }
                }, "删除" ),
              h( "Button", {
                props: {
                  type: "warning",
                  size: "small"
                },
                on: {
                  click() {
                    that.copy(params.row);
                  }
                }
              }, "复制" )
            ]);
          }
        }
      ],
      tuiguangTag: "全部",
      tagList: []
    };
  },
  mounted() {
    this.showDataList();
    this.showTagList();
  },
  methods: {
    showTagList() {
      this.$axios.get("/tuiguangTag").then(res => {
        this.tagList = res.data.data;
      });
    },
    showDataList() {
      this.$axios.get("/transfer").then(res => {
        let data = res.data.messages;
        this.domain_names = res.data.domain_names;
        if(this.tuiguangTag === '全部') {
          this.dataList = data;
        } else {
          this.dataList = [];
          for (let i = 0; i < data.length; i++) {
            if (this.tuiguangTag === data[i].group) {
              this.dataList.push(data[i]);
            }
          }
        }
        this.spreadData = this.dataList.slice(0, 10);
      });
    },
    copy(row) {
      row.id += '_copy';
      this.$axios.post('/transfer/create', row)
        .then(res => {
          if (res.data.success) {
            this.$Message.info('复制成功');
            this.spreadData.unshift(res.data.data);
          } else {
            this.$Message.info(res.data.err);
          }
        });
    },
    deleteOne(row, index) {
      let that = this;
      this.$Modal.confirm({
        title: '提示:',
        content: '确认删除这条内容吗？',
        onOk: () => {
          that.$axios.get('/transfer/delete', {params: {id: row._id}})
          .then(res => {
            that.$Message.info(res.data.success);
            that.spreadData.splice(index, 1)
          });
        }
      })
    },
    changePage(index){
      var _start = ( index - 1 ) * 10;
      var _end = index * 10;
      this.spreadData = this.dataList.slice(_start,_end);
      this.goTop()
    },
    goTop () {
      let self = this
      self.timer = setInterval(function () {
        let osTop = document.documentElement.scrollTop || document.body.scrollTop
        let ispeed = Math.floor(-osTop)
        document.documentElement.scrollTop = document.body.scrollTop = osTop + ispeed
        self.isTop = true
        if (osTop === 0) {
          clearInterval(self.timer)
        }
      }, 30)
    }
  },
  watch: {
    $route(to, from) {
      if(this.$route.params.state != 'cancel') {
        if(from.name == '创建推广链接') {
          this.spreadData.unshift(this.$route.params)
        } else if (from.name == '修改推广链接') {
          this.spreadData.splice(this.$route.params.index, 1, this.$route.params.row)
        }
      }
    }
  }
};
</script>


<style scoped>
  .spread-table {
    margin: 20px 0;
  }

  .tuiguang-tag {
    display: block;
    text-align: left;
    margin: 10px 0;
  }
</style>
